
import React, { useState, useEffect } from 'react';

// utils
import apiEntity from '../../../utils/apiEntity';

// styles
import {
  Container,
  ContainerHeader,
  TitleContainerLeft,
  TitleContainerRight,
  InputSearch,
  DropdownButton,
  DropdownMenu,
  DropdownItem
} from './EntityContainer.style';
// components
import EntityComponent from './entities/EntityComponent';
import CustomSelect from '../../../components/CustomSelect/CustomSelect';
import CustomButton from '../../../components/CustomButton/CustomButton';
import ModalEntities from './ModalEntities/ModalEntities';


const entityComponents = {
  contact: {
    label:              'Физическое лицо',
    inputSearchlabel:   'Номер телефона',
    inputSearchText:    'номер телефона',
    getEntity:          apiEntity.findContact,
  },
  company: {
    label:              'Юридическое лицо',
    inputSearchlabel:   'ИНН компании',
    inputSearchText:    'инн компании',
    getEntity:          apiEntity.findCompany,
  }
};


const EntityContainer = ({ label, role, doDuplicate, setDoDuplicate, dataDuplicate, setDataDuplicate }) => {
  const [entityType,          setEntityType]          = useState('');
  const [loadedEntityType,    setLoadedEntityType]    = useState('');
  const [newType,             setNewType]             = useState('');

  const [entityDetails,       setEntityDetails]       = useState(null);
  const [loadedEntityDetails, setLoadedEntityDetails] = useState(null);
  const [dataSearch,          setDataSearch]          = useState('');

  const [isOpen, setIsOpen] = useState(false);

  const [isModalOpen, setModalOpen] = useState(false);
  const openModal = () => setModalOpen(true);
  const closeModal = () => setModalOpen(false);

  const classRole = 'entity__' + role;

  useEffect(() => {
    
    const fetchData = async () => {
      const getData = apiEntity.fetchDataDetails;
      const data = await getData(role);

      const entityGetEntity = entityComponents[data.type]?.getEntity;
      const entityData = await entityGetEntity(data.value);

      setEntityDetails(entityData.details);
      setLoadedEntityDetails(entityData.details);
      setLoadedEntityType(entityData.details.type);
      setEntityType(data.type);
      setNewType(data.type);
      
    }

    fetchData();
  }, []);

  const handleTypeChange = () => {

  };

  const handleFindData = async () => {
    const entityGetEntity  = entityComponents[entityType]?.getEntity;
    const entityData = await entityGetEntity(dataSearch);
    console.log(entityData);
    if (entityData.found == true) {

      setEntityDetails(entityData.details);
      setLoadedEntityType(entityData.details.type);

    } else {
      openModal();
    }

    // console.log("1", entityDetails);
    // console.log("2", loadedEntityDetails);
  };

  const handleSelect = (key) => {
    setDoDuplicate(key);
    setDataDuplicate(entityDetails, entityType);
    setIsOpen(false); // Закрываем меню после выбора
  };

  useEffect(() => {

    const handleDuplicateData = async (entityDataDuplicate) => {
      const entityGetEntity  = entityComponents[entityDataDuplicate.type]?.getEntity;
      const entityData = await entityGetEntity(entityDataDuplicate.inn || entityDataDuplicate.phone);

      setEntityDetails(entityData.details);
      setLoadedEntityType(entityData.details.type);
      setEntityType(entityData.details.type);
      setNewType(entityData.details.type);
      apiEntity.saveEntityData(entityData.details, role);
      setLoadedEntityDetails(entityData.details);

      console.log("entityDetails", entityDetails);

    };

    
    if (doDuplicate && doDuplicate === role) {
      console.log('doDuplicate', doDuplicate);
      console.log('dataDuplicate', dataDuplicate);
      handleDuplicateData(dataDuplicate);
    }
  }, [doDuplicate]);
  
  const entityInputLabel  = entityComponents[entityType]?.inputSearchlabel;
  const entityInputText   = entityComponents[entityType]?.inputSearchText;
  const optionsSelect     = [
    { value: '', label: 'Выбрать' },
    ...Object.keys(entityComponents).map(key => ({
      value: key,
      label: entityComponents[key].label
    }))
  ];

  return (
    <Container className={classRole}>

      <ContainerHeader>
        <TitleContainerLeft style={{ fontWeight: 'bold', fontSize: '16px' }}>{label}</TitleContainerLeft>
        <TitleContainerRight>

        <div style={{ position: 'relative' }}>
          <DropdownButton onClick={() => setIsOpen(!isOpen)}>
            ...
          </DropdownButton>
          <DropdownMenu isOpen={isOpen}>
            {role && role !== 'sender' && (
              <DropdownItem onClick={() => handleSelect('sender')}>
                Дублировать в отправителя
              </DropdownItem>
            )}
            {role && role !== 'recipient' && (
              <DropdownItem onClick={() => handleSelect('recipient')}>
                Дублировать в получателя
              </DropdownItem>
            )}
            {role && role !== 'payer' && (
              <DropdownItem onClick={() => handleSelect('payer')}>
                Дублировать в плательщика
              </DropdownItem>
            )}
          </DropdownMenu>
        </div>

        </TitleContainerRight>
      </ContainerHeader>

      <ContainerHeader style={{ marginTop: '10px' }}>
        <TitleContainerLeft>Тип</TitleContainerLeft>
        <TitleContainerRight>
          <CustomSelect
            value={setEntityType}
            onChange={handleTypeChange}
            options={optionsSelect}
          />
        </TitleContainerRight>
      </ContainerHeader>

      {entityType && entityType !== '' && (
        <>

          <ContainerHeader style={{ marginTop: '5px' }}>
            <TitleContainerLeft>{entityInputLabel}</TitleContainerLeft>
            <TitleContainerRight>
              <InputSearch
                type="text"
                value={dataSearch}
                onChange={(event) => setDataSearch(event.target.value)}
                placeholder={`Введите ${entityInputText}`}
              />
            </TitleContainerRight>
          </ContainerHeader>

          <ContainerHeader style={{ marginTop: '5px' }}>
            <TitleContainerLeft></TitleContainerLeft>
            <TitleContainerRight>
              <CustomButton
                text="Найти в базе"
                padding={'6px'}
                color={'green'}
                onClick={handleFindData}
                style={{
                  width: '188px'
                }}
              />
            </TitleContainerRight>
          </ContainerHeader>

        </>
      )}

      {entityDetails && (
        <>
          <EntityComponent
            type={entityType}
            loadedType={loadedEntityType}
            setLoadedEntityType={setLoadedEntityType}
            newType={newType}
            setNewType={setNewType}
            data={entityDetails}
            setData={setEntityDetails}
            onAttach={apiEntity.saveEntityData}
            role={role}
            loadedEntityDetails={loadedEntityDetails}
            setLoadedEntityDetails={setLoadedEntityDetails}
          />
        </>
      )}

      <ModalEntities
        isOpen={isModalOpen}
        onClose={closeModal}
        type={entityType}
        dataSearch={dataSearch}
        setDataSearch={setDataSearch}
        entityComponents={entityComponents}
      />

    </Container>
    
  )
};

export default EntityContainer;
