

import React, { useState, useEffect } from 'react'
import ReactDOM from 'react-dom/client';

// utils

// styles

// components
import DocsContainer from './containers/Contract/DocsContainer';
import ActContainer from './containers/Act/ActContainer';
import InvoiceContainer from './containers/Invoice/InvoiceContainer';
import EkspedContainer from './containers/Eksped/EkspedContainer';
import LabelContainer from './containers/Label/LabelContainer';
import KoContainer from './containers/KO/KoContainer';

const DocsIds = {
  contract: 1193041, // договор 1193041
  act:      1586669, // акт 1586669
  invoice:  1586667, // счёт 1586667
  eksped:   1410899, // экспед 1410899
  label:    1372511, // этикетка 1372511
  ko:       1369297 // ко 1369297
}

const DocsContainers = () => {

  useEffect(() => {

    const DocsContainerDiv = document.querySelector(`[data-id="${DocsIds.contract}"]`);
    if (DocsContainerDiv) {
      const parent = DocsContainerDiv.parentElement;
      const div = document.createElement('div');
      parent.replaceChild(div, DocsContainerDiv);
      const root = ReactDOM.createRoot(div);
      root.render(
        <DocsContainer type='contract' />
      );
    }

    const ActContainerDiv = document.querySelector(`[data-id="${DocsIds.act}"]`);
    if (ActContainerDiv) {
      const parent = ActContainerDiv.parentElement;
      const div = document.createElement('div');
      parent.replaceChild(div, ActContainerDiv);
      const root = ReactDOM.createRoot(div);
      root.render(
        <ActContainer type='act' />
      );
    }

    const InvoiceContainerDiv = document.querySelector(`[data-id="${DocsIds.invoice}"]`);
    if (InvoiceContainerDiv) {
      const parent = InvoiceContainerDiv.parentElement;
      const div = document.createElement('div');
      parent.replaceChild(div, InvoiceContainerDiv);
      const root = ReactDOM.createRoot(div);
      root.render(
        <InvoiceContainer type='invoice' />
      );
    }

    const EkspedContainerDiv = document.querySelector(`[data-id="${DocsIds.eksped}"]`);
    if (EkspedContainerDiv) {
      const parent = EkspedContainerDiv.parentElement;
      const div = document.createElement('div');
      parent.replaceChild(div, EkspedContainerDiv);
      const root = ReactDOM.createRoot(div);
      root.render(
        <EkspedContainer type='eksped' />
      );
    }

    const LabelContainerDiv = document.querySelector(`[data-id="${DocsIds.label}"]`);
    if (LabelContainerDiv) {
      const parent = LabelContainerDiv.parentElement;
      const div = document.createElement('div');
      parent.replaceChild(div, LabelContainerDiv);
      const root = ReactDOM.createRoot(div);
      root.render(
        <LabelContainer type='label' />
      );
    }

    const KoContainerDiv = document.querySelector(`[data-id="${DocsIds.ko}"]`);
    if (KoContainerDiv) {
      const parent = KoContainerDiv.parentElement;
      const div = document.createElement('div');
      parent.replaceChild(div, KoContainerDiv);
      const root = ReactDOM.createRoot(div);
      root.render(
        <KoContainer type='ko' />
      );
    }

  }, []);

  return (
    <>

    </>
  );

};

export default DocsContainers;