
import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';

// utils
import EntityAPI from '../../../../utils/apiEntity.js';
import entityLabels from '../entities/EntityLabels.js';

// styles
import {
  ModalOverlay,
  ModalContent,
  LineContainerLeft,
  LineContainerRight,
  InfoLine,
  InputSave
} from './ModalEntities.style.js';

// components
import CustomButton from '../../../../components/CustomButton/CustomButton.jsx';


const ModalEntities = ({ isOpen, onClose, type, dataSearch, setDataSearch, entityComponents }) => {
  const [dataForSave, setDataForSave] = useState({});
  const [formData, setFormData] = useState({
    address:        '',
    name:           '',
    contactPerson:  '',
    position:       '',
    kpp:            '',
    company_phone:  '',
    biс:            '',
    bank:           '',
    ks:             '',
    rs:             '',
    ogrn:           ''
  });

  useEffect(() => {
    setFormData({
      address:        '',
      name:           '',
      contactPerson:  '',
      position:       '',
      kpp:            '',
      company_phone:  '',
      biс:            '',
      bank:           '',
      ks:             '',
      rs:             '',
      ogrn:           ''
    });

    setDataSearch('');
    setDataForSave({});

  }, [type]);

  if (!isOpen) return null;

  const labels = entityLabels[type];
  const mainLabel = entityComponents[type].inputSearchlabel;
  const typeAddLabel = entityComponents[type].label;

  const handleFindDadata = async () => {
    const getCompanyByINN = EntityAPI.fetchCompanyByINN;
    const dataCompany = await getCompanyByINN(dataSearch);
    const dataCompanyResult = JSON.parse(dataCompany);
    const companyData = dataCompanyResult.suggestions[0].data;

    // console.log(companyData);
    
    const getBankDataByINN = EntityAPI.fetchBankDataByINN;
    const dataBank = await getBankDataByINN(dataSearch);
    const dataBankResult = JSON.parse(dataBank);
    let bankData = null;
    
    // console.log(dataBankResult);

    if (dataCompanyResult) {
      const formData = {
        name:           companyData.name.short_with_opf,
        address:        companyData.address.value,
        contactPerson:  companyData.management.name,
        position:       companyData.management.post,
        ogrn:           companyData.ogrn,
        kpp:            companyData.kpp,
      };

      if (dataBankResult && dataBankResult.suggestions && dataBankResult.suggestions.length > 0 && dataBankResult.suggestions[0].data) {
        bankData = dataBankResult.suggestions[0].data;
  
        if (Object.keys(dataBankResult).length > 0) {
          formData.bic  = bankData.bic || '';
          formData.bank = dataBankResult.suggestions[0].value || '';
          formData.ks   = bankData.correspondent_account || '';
          formData.rs   = bankData.treasury_accounts?.[0] || '';
        }
      }
  
      setFormData(formData);
      setDataForSave(formData);
    }
  };

  const handleSaveEntity = async () => {

    let formattedDataSearch = dataSearch;

    if (type === 'contact') {
      const cleanedNumber = dataSearch.replace(/[^\d]/g, '');
      let formattedNumber = cleanedNumber.replace(/^8/, '7');
      if (/^9\d{9}$/.test(formattedNumber)) {
        formattedNumber = '7' + formattedNumber;
      }
      formattedDataSearch = formattedNumber;
    }

    const entity = entityLabels[type];
    const entityFieldMain =  Object.keys(entity)[0];

    const secondDataForSave = { ...dataForSave, [entityFieldMain]: formattedDataSearch };
    const updatedDataForSave = { ...secondDataForSave, type: type };

    // console.log(updatedDataForSave);
    const saveNewEntity = EntityAPI.saveEntity;
    const dataBank = await saveNewEntity(updatedDataForSave);

    onClose();
  }

  const handleInputChange = (key, value) => {
    const updatedDataForSave = { ...dataForSave, [key]: value };
    setFormData((prevFormData) => ({ ...prevFormData, [key]: value }));
    setDataForSave(updatedDataForSave);
  };

  const setSearch = (e) => {
    setDataSearch(e.target.value);
  }

  return ReactDOM.createPortal(
    <ModalOverlay>
      <ModalContent onClick={e => e.stopPropagation()}>

        <InfoLine style={{ marginBottom: '20px' }}>
          <LineContainerLeft style={{ fontWeight: 'bold' }}>
            Добавление в базу: {typeAddLabel}
          </LineContainerLeft>

          <LineContainerRight style={{ flex: 0 }}>
              <CustomButton
                text="Закрыть"
                padding={'3px'}
                color={'red'}
                onClick={onClose}
                style={{
                  width: '70px'
                }}
              />
          </LineContainerRight>
        </InfoLine>
        
        <InfoLine>
          <LineContainerLeft>
            {mainLabel}
          </LineContainerLeft>
          <LineContainerRight>
            <InputSave
              type="text"
              value={dataSearch}
              onChange={setSearch}
              style={{
                width: '120px',
                marginRight: '10px'
              }}
            />

            { type !== 'contact' && (
              <CustomButton
                text="Найти в Dadata"
                padding={'6px'}
                color={'blue'}
                onClick={handleFindDadata}
                style={{
                  width: '150px'
                }}
              />
            )}

          </LineContainerRight>
        </InfoLine>

        <div style={{ marginTop: '15px' }}>
          <p style={{ fontWeight: 'bold' }}>Обязательные поля:</p>
          {Object.keys(labels).slice(1, 8).map((key) => (
            <InfoLine key={key} style={{ marginTop: '10px' }}>
              <LineContainerLeft>
                {labels[key]}
              </LineContainerLeft>
              <LineContainerRight>
                <InputSave
                  type="text"
                  value={formData[key]}
                  onChange={(e) => handleInputChange(key, e.target.value)}
                />
              </LineContainerRight>
            </InfoLine>
          ))}
        </div>
        
        { type !== 'contact' && (
          <div style={{ marginTop: '15px' }}>
            <p style={{ fontWeight: 'bold' }}>Необязательные поля:</p>
            {Object.keys(labels).slice(8).map((key) => (
              <InfoLine key={key} style={{ marginTop: '10px' }}>
                <LineContainerLeft>
                  {labels[key]}
                </LineContainerLeft>
                <LineContainerRight>
                  <InputSave
                    type="text"
                    value={formData[key]}
                    onChange={(e) => handleInputChange(key, e.target.value)}
                  />
                </LineContainerRight>
              </InfoLine>
            ))}
          </div>
        )}

        <InfoLine>
          <LineContainerLeft></LineContainerLeft>
          <LineContainerRight style={{ flex: 0 }} >
            <CustomButton
              text="Сохранить сущность в базе"
              padding={'6px'}
              color={'green'}
              onClick={handleSaveEntity}
              style={{
                width: '188px',
                marginTop: '20px',
                justifyContent: 'flex-end'
              }}
            />
          </LineContainerRight>
        </InfoLine>
        
      </ModalContent>
    </ModalOverlay>,
    document.body
  );
};

export default ModalEntities;