
import React, { useEffect, useState, useMemo } from 'react';

// utils
import DocsAPI from '../../../../utils/apiDocs';

// styles
import {
  Container,
  ContainerHeader,
  ContainerLeft,
  ContainerRight,
  ContainerCenter,
  InputSearch
} from '../Contract/DocsContainer.style';

// components
import CustomFields from '../CustomFields/CustomFields';
import FilesContainer from '../FilesContainer/FilesContainer';
import CustomButton from '../../../../components/CustomButton/CustomButton'


const DocsLabels = {
  invoice: {
    name: 'Счёт',
    typeDocs: 'invoice',
    createDocs: DocsAPI.createInvoice,
  }
};

const customFields = [
  { name: 'Отправитель', entity: 'sender' },
  { name: 'Получатель',  entity: 'recipient' },
  { name: 'Плательщик',  entity: 'payer' }
];

const InvoiceContainer = ({ type }) => {
  const [allDataFilled, setAllDataFilled] = useState(false);
  const [allEntitiesFilled, setAllEntitiesFilled] = useState(false);
  const [isContract, setIsContract] = useState(false);
  const [tableValues, setTableValues] = useState({});
  const [dataDocs, setDataDocs] = useState(null);
  const [lastContract, setLastContract] = useState('');

  const [isLoading, setIsLoading] = useState(false); // новое состояние

  const [entities, setEntities] = useState({
    sender: {},
    recipient: {},
    payer: {},
  });


  useEffect(() => {

    fetchDataInfo();

    const observer_services_1 = new MutationObserver((mutationsList, observer) => {
      for (let mutation of mutationsList) {
        if (mutation.type === 'childList') {
          const element = document.querySelector('.services');
          if (element) {
            console.log('Element found:', element);

            extractServices();

            const servicesContainer = document.querySelector('.services');
            if (servicesContainer) {
              const observer_services_2 = new MutationObserver(extractServices);
              observer_services_2.observe(servicesContainer, { childList: true, subtree: true });

              observer_services_1.disconnect(); // Останавливаем первый наблюдатель
              return () => observer_services_2.disconnect();
            }

            observer_services_1.disconnect();
            break;
          }
        }
      }
    });

    observer_services_1.observe(document.documentElement, { childList: true, subtree: true });


    const inputElement = document.querySelector('input[name="CFV[1598079]"]'); //ручной договор
    if (inputElement.value.trim() !== "") {
        console.log("Поле заполнено");
        setLastContract(inputElement.value.trim());
        setIsContract(true);
    } else {
      console.log("Поле пустое");
      // тут ищем последний договор
      const contractTitleObserver = new MutationObserver(() => {
        const contractTitleElement = document.querySelector('.contract__list_docs');
        if (contractTitleElement) {
          const docsRowLast = contractTitleElement.querySelector('.data-list_1');
          if (docsRowLast) {
            const titleElement = docsRowLast.querySelector('.list__row__title');
            const dateElement = docsRowLast.querySelector('.list__row__date');
            if (titleElement && dateElement) {
              const hasTitle = titleElement.textContent.trim();
              const hasDate = dateElement.textContent.trim();
              setLastContract(`${hasTitle} от ${hasDate.slice(0, -10)}`);
              setIsContract(true);
            }
          } else {
            setLastContract('');
            setIsContract(false);
          }
        } else {
          setLastContract('');
          setIsContract(false);
        }
      });
      
      const contractTitleElement = document.querySelector('.contract__list_docs');
      if (contractTitleElement) {
        contractTitleObserver.observe(contractTitleElement, { childList: true, subtree: true });
      }
    }

    return () => {
      observer_services_1.disconnect(); // Очистка при размонтировании
      contractTitleObserver.disconnect(); // Очистка при размонтировании
    };
  }, []);

  useEffect(() => {

    const observer_entities_1 = new MutationObserver((mutationsList, observer) => {
      for (let mutation of mutationsList) {
        if (mutation.type === 'childList') {
          const element = document.querySelector('.entities');
          if (element) {
            console.log('Element found:', element);

            extractEntities();

            const entitiesContainer = document.querySelector('.entities');
            if (entitiesContainer) {
              const observer_entities_2 = new MutationObserver(extractEntities);
              observer_entities_2.observe(entitiesContainer, { childList: true, subtree: true });

              observer_entities_1.disconnect(); // Останавливаем первый наблюдатель
              return () => observer_entities_2.disconnect();
            }

            observer_entities_1.disconnect();
            break;
          }
        }
      }
    });

    observer_entities_1.observe(document.documentElement, { childList: true, subtree: true });

    return () => {
      observer_entities_1.disconnect(); // Очистка при размонтировании
    };
  }, []);

  useEffect(() => {
    checkAllFieldsFilled(entities);
  }, [entities]);

  const extractServices = () => {
    const servicesArray = [];

    document.querySelectorAll('.services__row').forEach(row => {
      const titleElement = row.querySelector('.services__row__title');
      const countElement = row.querySelector('.services__row__count');
      const priceElement = row.querySelector('.services__row__price');

      if (titleElement && countElement && priceElement) {
        const title = titleElement.textContent.trim();
        const count = Number(countElement.value);
        const price = Number(priceElement.value);
  
        servicesArray.push({ title, count, price });
      }
    });

    if (Object.keys(servicesArray).length > 0) {
      setAllDataFilled(true);
    } else {
      setAllDataFilled(false);
    }

    setTableValues(servicesArray);
    console.log(servicesArray);
  };

  const extractEntities = () => {
    const newEntities = {
      sender: {},
      recipient: {},
      payer: {},
    };

    document.querySelectorAll('.entity__sender, .entity__recipient, .entity__payer').forEach(entity => {
      const entityClass = entity.classList.contains('entity__sender') ? 'sender' :
        entity.classList.contains('entity__recipient') ? 'recipient' : 'payer';

      entity.querySelectorAll('[class*="entity__key__"]').forEach(item => {
        const key = item.className.split(' ').find(cls => cls.startsWith('entity__key__')).replace('entity__key__', '').toLowerCase();
        newEntities[entityClass][key] = item.textContent;
      });
    });

    setEntities(newEntities);
  };

  const checkAllFieldsFilled = (newEntities) => {
    const areEntitiesFilled = customFields.every(field => Object.keys(newEntities[field.entity]).length > 0);
    setAllEntitiesFilled(areEntitiesFilled);
  };

  const fetchDataInfo = async () => {
    const fetchInfoDocs = DocsAPI.fetchDocsInfo;
    const dataInfoDocs = await fetchInfoDocs(DocsLabels[type].typeDocs);
    setDataDocs(dataInfoDocs);
    console.log('dataInfoDocs', dataInfoDocs);
  }


  const handleGenerateDoc = async () => {

    let dataReal = null;

    const dateElement = document.querySelector(`input[name="CFV[133081]"]`);
    if (dateElement) {
      let value = dateElement.value;
      if (!value) {
        dataReal = 'ignore'; 
      } else {
        dataReal = value;
      }
    }

    setIsLoading(true); // установить состояние загрузки
    const invoiceData = {
      entities,
      values: tableValues,
      lastContract: lastContract,
      dataReal: dataReal
    };

    if (allDataFilled && isContract && allEntitiesFilled) {
      console.log('Invoice Data:', invoiceData);

      const postData = async () => {
        const postDataDocs = DocsLabels[type].createDocs;
        await postDataDocs(invoiceData, type);
        await fetchDataInfo();
      }
      await postData();
    }
    setIsLoading(false); // снять состояние загрузки
  };





  return (
    <Container>

      <ContainerHeader style={{ marginBottom: '15px' }}>
        <ContainerLeft style={{ fontWeight: 'bold', fontSize: '16px' }}>{DocsLabels[type].name}</ContainerLeft>
        <ContainerRight>Sett.</ContainerRight>
      </ContainerHeader>

      <Container style={{ padding: '5px 10px 10px 10px', borderRadius: '6px' }}>
        <FilesContainer dataDocs={dataDocs} />
      </Container>

      <ContainerHeader style={{ alignItems: 'flex-start' }}>
        <ContainerLeft style={{ flex: 0 }}>
          <CustomButton
            text={isLoading ? 'Загрузка...' : 'Сгенерировать счёт'}
            padding={'4px'}
            color={allDataFilled && isContract && allEntitiesFilled ? 'green' : 'red'}
            onClick={handleGenerateDoc}
            style={{
              width: '180px',
              margin: '15px 0 15px 0',
              opacity: isLoading ? 0.7 : 1, // уменьшить прозрачность кнопки во время загрузки
              cursor: isLoading ? 'progress' : 'pointer' // изменить курсор во время загрузки
            }}
            disabled={isLoading} // отключить кнопку во время загрузки
          />
        </ContainerLeft>
        <ContainerRight style={{ display: 'block', color: 'red', paddingLeft: '15px', flex: 2 }}>
          {!isContract && (
            <p style={{
              display: 'block',
              marginTop: '10px',
              fontSize: '14px'
            }}>
              Cгенерируйте договор, для доступа к генерации счёта
            </p>
          )}
          {!allDataFilled && (
            <p style={{
              display: 'block',
              marginTop: '10px',
              fontSize: '14px'
            }}>
              Заполните таблицу услуг, для доступа к генерации счёта
            </p>
          )}
          {!allEntitiesFilled && (
            <p style={{
              display: 'block',
              marginTop: '10px',
              fontSize: '14px'
            }}>
              Заполните все сущности, для доступа к генерации счёта
            </p>
          )}
        </ContainerRight>
      </ContainerHeader>
      

    </Container>
  )
};

export default InvoiceContainer;
