import React, { useEffect, useState } from 'react';

// utils
import DocsAPI from '../../../../utils/apiDocs';

// styles
import {
  Container,
  ContainerHeader,
  ContainerLeft,
  ContainerRight,
  ContainerCenter,
  InputSearch
} from '../Contract/DocsContainer.style';

// components
import FilesContainer from '../FilesContainer/FilesContainer';
import CustomButton from '../../../../components/CustomButton/CustomButton';
import CustomFields from '../CustomFields/CustomFields';

const DocsLabels = {
  label: {
    name: 'Этикетка',
    fields: [
      { nameId: 'otkuda',         id: 179029, typeValue: 'select',        name: 'Откуда' },
      { nameId: 'kuda',           id: 179035, typeValue: 'select',        name: 'Куда' },
      { nameId: 'kuda_tochno',    id: 179037, typeValue: 'textarea',      name: 'Куда точно' },
      { nameId: 'vid_transporta', id: 133151, typeValue: 'select',        name: 'Вид транспорта' },
      { nameId: 'kolvo_mest',     id: 179505, typeValue: 'number',        name: 'Кол-во мест' },
    ],
    typeDocs: 'label',
    createDocs: DocsAPI.createLabel,
  }
};

const LabelContainer = ({ type }) => {
  const [isEksped,        setIsEksped]        = useState(true);
  const [allFieldsFilled, setAllFieldsFilled] = useState(false);
  const [fieldValues,     setFieldValues]     = useState({});
  const [dataDocs,        setDataDocs]        = useState(null);
  const [lastEksped,      setLastEksped]      = useState('');
  const [lastData,        setLastData]        = useState('');
  const { name, fields } = DocsLabels[type];

  const [isLoading, setIsLoading] = useState(false); // новое состояние

  useEffect(() => {
    fetchDataInfo();

    // тут ищем последнюю экспедиторку
    const ekspedTitleObserver = new MutationObserver(() => {
      const contractTitleElement = document.querySelector('.eksped__list_docs');
      if (contractTitleElement) {
        const docsRowLast = contractTitleElement.querySelector('.data-list_1');
        if (docsRowLast) {
          const titleElement = docsRowLast.querySelector('.list__row__title');
          const dateElement = docsRowLast.querySelector('.list__row__date');
          if (titleElement) {
            const hasTitle = titleElement.textContent.trim();
            const hasDate = dateElement.textContent.trim();
            setLastEksped(hasTitle.substring(1));
            setLastData(`${hasDate.slice(0, -10)}`);
            // setIsEksped(true);
          }
        } else {
          setLastEksped('');
          setLastData('');
          // setIsEksped(false);
        }
      } else {
        setLastEksped('');
        setLastData('');
        // setIsEksped(false);
      }
    });

    const contractTitleElement = document.querySelector('.eksped__list_docs');
    if (contractTitleElement) {
      ekspedTitleObserver.observe(contractTitleElement, { childList: true, subtree: true });
    }

    return () => {
      ekspedTitleObserver.disconnect(); // Очистка при размонтировании
    };
  }, []);

  const checkAllFieldsFilled = () => {
    // let areFieldsFilled = false;
    // const labelListDocs = document.querySelector('.label_fields__container');
    // if (labelListDocs) {
    //   const fieldElements = labelListDocs.querySelectorAll('[class*="field__biba_id_"]');
    //   areFieldsFilled = Array.from(fieldElements).every(field => {
    //     return field && field.textContent.trim() !== '' && field.textContent.trim() !== 'Выбрать';
    //   });
    // } else {
    //   areFieldsFilled = false;
    // }
    let areFieldsFilled = true;
    setAllFieldsFilled(areFieldsFilled);
  };

  const handleFieldChange = (id, value) => {
    setFieldValues(prevValues => ({
      ...prevValues,
      [id]: value
    }));
    checkAllFieldsFilled();
  };

  const fetchDataInfo = async () => {
    const fetchInfoDocs = DocsAPI.fetchDocsInfo;
    const dataInfoDocs = await fetchInfoDocs(DocsLabels[type].typeDocs);
    setDataDocs(dataInfoDocs);
    console.log('dataInfoDocs', dataInfoDocs);
  };

  const handleGenerateDoc = async () => {
    setIsLoading(true); // установить состояние загрузки
    const actData = {
      fields: fieldValues,
      lastEksped: lastEksped,
      lastData: lastData
    };

    if (isEksped && allFieldsFilled) {
      console.log('Label Data:', actData);

      const postData = async () => {
        const postDataDocs = DocsLabels[type].createDocs;
        await postDataDocs(actData, type);
        await fetchDataInfo();
      };
      await postData();
    }
    setIsLoading(false); // снять состояние загрузки
  };

  useEffect(() => {
    checkAllFieldsFilled();
  }, [fieldValues]);

  return (
    <Container>
      <ContainerHeader style={{ marginBottom: '15px' }}>
        <ContainerLeft style={{ fontWeight: 'bold', fontSize: '16px' }}>{DocsLabels[type].name}</ContainerLeft>
        <ContainerRight>Sett.</ContainerRight>
      </ContainerHeader>

      <Container style={{ padding: '5px 10px 10px 10px', borderRadius: '6px' }}>
        <FilesContainer dataDocs={dataDocs} />
      </Container>

      <ContainerHeader style={{ alignItems: 'flex-start' }}>
        <ContainerLeft style={{ flex: 0 }}>
          <CustomButton
            text={isLoading ? 'Загрузка...' : 'Сгенерировать этикетку'} // изменить текст кнопки во время загрузки
            padding={'4px'}
            color={isEksped && allFieldsFilled ? 'green' : 'red'}
            onClick={handleGenerateDoc}
            style={{
              width: '180px',
              margin: '15px 0 15px 0',
              opacity: isLoading ? 0.7 : 1, // уменьшить прозрачность кнопки во время загрузки
              cursor: isLoading ? 'progress' : 'pointer' // изменить курсор во время загрузки
            }}
            disabled={isLoading} // отключить кнопку во время загрузки
          />
        </ContainerLeft>
        <ContainerRight style={{ display: 'block', color: 'red', paddingLeft: '15px', flex: 2 }}>
          {!isEksped && (
            <p style={{
              display: 'block',
              marginTop: '10px',
              fontSize: '14px'
            }}>
              Cгенерируйте экспедиторкую расписку, для доступа к генерации этикетки
            </p>
          )}
        </ContainerRight>
      </ContainerHeader>

      <Container style={{ padding: '5px 10px 10px 10px', borderRadius: '6px' }}>
        <div className="label_fields__container">
          {fields.map(field => (
            <ContainerHeader key={field.id}>
              <CustomFields
                id={field.id}
                type={field.typeValue}
                name={field.name}
                nameId={field.nameId}
                onFieldChange={handleFieldChange}
              />
            </ContainerHeader>
          ))}
        </div>
      </Container>

    </Container>
  );
};

export default LabelContainer;